  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}

.control {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 46px;
  padding-left: 62px;
  cursor: pointer;
}
.control:hover .control__mark {
  background-color: #e5e5e5;
}
.control:active .control__mark {
  background-color: #d5d5d5;
}
.control:active .control__mark::before {
  opacity: 0.3;
}

.control__label {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  color: #000000;
}

.control__mark {
  position: absolute;
  left: 0;
  top: 0;
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: #f2f2f2;
}
.control__mark--impression {
  border-radius: 50%;
}

.control__input[type=radio]:checked + .control__mark::before {
  position: absolute;
  content: "";
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
}

.control__input[type=checkbox]:checked + .control__mark::before {
  position: absolute;
  content: "";
  top: 15px;
  left: 12px;
  width: 22px;
  height: 16px;
  background-image: url("../../img/tick.svg");
  background-repeat: no-repeat;
}

